
.swiper {
  width: 90%;
  height: auto;
  min-height: 75vh;
  background-color: transparent;
}
/*-Contenedor de cartas-*/
.swiper-slide {
  padding: 10px 10px;
  border-radius: 15px;
  user-select: none;
}
.card-image {
  width: 100%;
  aspect-ratio: 16/9;
  object-fit: cover;
  border-radius: 15px;
}
.badge-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  background-color: transparent;
  gap: 4px;
}
.badge {
    margin: 16px 0px 18px;
    padding: 8px 14px;
    width: fit-content;
    border-radius: 50px;
    font-size: 12.5px;
    font-weight: bolder;
    transition: all 0.3s ease;
  }
.badge:hover {
  color:black;
}
.card-title {
  padding: 10px 10px;
  font-size: 1.3rem;
  font-style: italic;
  border-radius: 10px;
}
.card-title .name-slider {
  background-color: transparent;
}
.card1 {
  box-shadow: 3px 3px 10px rgba(255, 0, 157, 0.1),
    -3px -3px 10px rgba(255, 0, 157, 0.1);
  border: 3px solid rgba(218, 0, 76, 0.8);
  background-color: rgba(255, 0, 157, 0.03);
}

.card1 .badge {
